/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
  border: 0; }

* {
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote::before, blockquote::after {
  content: '';
  content: none; }

q::before, q::after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.truncate {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
  .truncate_title {
    height: 4em; }
  .truncate_subtitle {
    width: 7em; }
  .truncate_text {
    width: 20.43em; }
  .truncate_label {
    width: 50%; }

.title {
  font-weight: 400;
  font-size: 3.0625em;
  line-height: 1.28571;
  color: #fff;
  font-family: 'PT Sans', sans-serif; }

.subtitle {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 1.18182;
  color: #111; }

.text {
  font-weight: 300;
  font-size: 0.875em;
  line-height: 1.14286;
  color: #111; }
  .text a {
    color: #5a61ed;
    font-weight: 500; }
  .text_size_smaller {
    font-size: 0.8125em;
    line-height: 1.05; }

.btn {
  width: 11em;
  height: 2.285em;
  color: #fff;
  font-weight: 400;
  font-size: 0.875em;
  font-family: 'PT Sans', sans-serif;
  line-height: 2.285em;
  text-align: center;
  background-color: #5a61ed;
  border: none; }
  .btn:active, .btn:focus {
    outline: none; }
  .btn:hover {
    cursor: pointer; }

.page-signup {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  min-height: 500px;
  padding: 20px;
  background-color: #f4f5fc; }

.form {
  font-size: 1rem;
  display: flex;
  width: 100%;
  max-width: 665px;
  box-shadow: 0 12px 82px rgba(0, 0, 0, 0.2); }
  .form__title {
    position: relative;
    width: 20.6%;
    min-width: 70px;
    padding-top: 20px;
    padding-left: 6px;
    background-color: #5a61ed;
    transform: scale(-1);
    writing-mode: vertical-lr; }
  .form__data-entry {
    position: relative;
    width: 79.4%;
    padding: 38px 20px 32px 20px;
    font-family: 'Work Sans', sans-serif;
    background-color: #fff; }
  .form__subtitle {
    margin-bottom: 5px; }
  .form__text {
    margin-bottom: 20px; }
  .form__content {
    position: relative;
    z-index: 5;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 100px; }
  .form__block {
    position: relative;
    display: inline-block;
    margin-bottom: 15px; }
    .form__block_size_xl {
      width: 100%;
      max-width: 235px; }
    .form__block_size_sm {
      width: 24%; }
    .form__block_size_lg {
      width: 38%;
      margin-right: 3%;
      margin-left: 3%; }
    .form__block_size_md {
      width: 27%; }
  .form__label {
    font-weight: 300;
    font-size: 0.75em;
    line-height: 1.16667;
    color: #7c7c7c; }
    .form__label_for-radio {
      margin-bottom: 5px; }
  .form__icon-valid {
    position: absolute;
    right: 0;
    bottom: 13px;
    width: 11px;
    height: 8px;
    transition: stroke-dashoffset 0.5s ease-in;
    fill: none;
    stroke: #5a61ed;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 15; }
  .form__icon-list-arrow {
    position: absolute;
    right: 0;
    bottom: 13px;
    z-index: 1;
    width: 8px;
    height: 4px;
    fill: none;
    stroke: #111; }
  .form__input, .form__select, .form__radio-label {
    font-weight: 400;
    font-size: 0.875em;
    line-height: 1.14286;
    width: 100%;
    padding-top: 5px;
    padding-right: 20px;
    padding-bottom: 6px;
    color: #111;
    background: transparent;
    border: none;
    border-bottom: 1px solid #f2f2f2; }
    .form__input:focus, .form__select:focus, .form__radio-label:focus {
      outline: none; }
  .form__select {
    position: relative;
    z-index: 5;
    appearance: none; }
    .form__select:hover {
      cursor: pointer; }
  .form__radio-label {
    position: relative;
    border-bottom: none; }
    .form__radio-label::before {
      position: absolute;
      top: 4px;
      left: -18px;
      display: block;
      width: 12px;
      height: 12px;
      border: 1px solid #5a61ed;
      border-radius: 50%;
      content: ''; }
    .form__radio-label::after {
      position: absolute;
      top: 8px;
      left: -14px;
      display: block;
      width: 6px;
      height: 6px;
      background-color: #5a61ed;
      border-radius: 50%;
      transform: scale(0);
      transition: transform 0.2s;
      content: ''; }
    .form__radio-label:hover {
      cursor: pointer; }
  .form__radio {
    appearance: none; }
    .form__radio:checked ~ label::after {
      transform: scale(1); }
  .form__footer {
    position: relative;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .form__icon-user-bg {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 1;
    width: 411px;
    height: 411px;
    animation: draw-user-bg 3s ease-in 1s forwards;
    fill: #f9faff;
    stroke: #e0e5ff;
    fill-opacity: 0;
    stroke-dasharray: 3810;
    stroke-dashoffset: 3810; }

@keyframes draw-user-bg {
  80% {
    stroke-dashoffset: 0;
    fill-opacity: 0;
    stroke: #e0e5ff; }
  100% {
    stroke-dashoffset: 0;
    fill-opacity: 1;
    stroke: #f9faff; } }
